<script>
import functions from "../../util/functions.js";
import constants from "../../util/constants";
export default {
  props: {
    add_mode: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    ...functions,
    verifierChargement() {
      this.actCharge += 1;
      if (this.actCharge == this.totalChargement) {
        this.is_loading_categorie_faqs = false;
      }
    },
    verifierQuestion(categorie_faq) {
      let message = "";
      if (categorie_faq.libelle == undefined || categorie_faq.libelle == -1) {
        message = "Veuillez entrer un libellé correcte svp";
      }
      if (message != "") {
        this.$swal({
          type: "warning",
          title: "Les données entrées sont incorrectes",
          text: message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      return {
        status: message === "",
        message,
      };
    },

    async doAddCategorieFaq() {
      if (!this.verifierQuestion(this.categorie_faq).status) return;
      this.loading_message = "Enregistrement  en cours";
      this.is_doing_action = true;
      let url = this.constants.api_url + "/categories_faqs/add.php";
      let response = null;

      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        response = await fetch(url, {
          method: "post",
          cache: "no-cache",
          credentials: "include",
          headers,
          body:
            "libelle=" +
            encodeURIComponent(this.categorie_faq.libelle),
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS") {
        this.categorie_faq = this.default_categorie_faq;
        this.$swal({
          type: "success",
          title: "Effectué",
          text: "L'enregistrement a été effectué avec succès. ",
          //footer: '<a href>Why do I have this issue?</a>'
        });
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
    async doEditQuestion() {
      if (!this.verifierQuestion(this.categorie_faq).status) return;

      this.loading_message = "Enregistrement des modifications  en cours";
      this.is_doing_action = true;
      let url = this.constants.api_url + "/categories_faqs/edit.php";
      let response = null;

      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        response = await fetch(url, {
          method: "post",
          headers,
          credentials: "include",
          cache: "no-cache",
          body:
            "id_categorie_faq=" +
            encodeURIComponent(this.$route.params.id_categorie_faq) +
            "&libelle=" +
            encodeURIComponent(this.categorie_faq.libelle) ,
        });
      } catch (e) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: e.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      if (data.status == "SUCCESS") {
        await this.$swal({
          type: "success",
          title: "Effectué",
          text: "Les modifications ont été enregistrées avec succès. ",
          //footer: '<a href>Why do I have this issue?</a>'
        });
        this.$router.push("/admin/faqs/categories-faqs");
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
      }
      this.is_doing_action = false;
    },
  },
  async created() {
    if (!this.add_mode) {
      this.totalChargement += 1;
    } 

    if (!this.add_mode) {
      this.is_loading_categorie_faqs = true;
      let url =
        this.constants.api_url +
        "/categories_faqs/get.php?id_categorie_faq=" +
        encodeURIComponent(this.$route.params.id_categorie_faq);
      let response = null;
      try {
        let headers = new Headers();
        headers.append("Content-type", "application/x-www-form-urlencoded");
        response = await fetch(url, {
          method: "get",
          cache: "no-cache",
          credentials: "include",
          headers,
        });
      } catch (err) {
        this.$swal({
          type: "error",
          title: "La connexion au serveur a échoué",
          text: err.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        return;
      }
      let data = await response.json();
      console.log(data);
      if (data.status == "SUCCESS" && data.categorie_faq) {
        this.categorie_faq = data.categorie_faq;
        this.verifierChargement();
      } else {
        this.$swal({
          type: "error",
          title: "Une erreur est survenue",
          text: "Status : " + data.status + "<br/>Message : " + data.message,
          //footer: '<a href>Why do I have this issue?</a>'
        });
        this.verifierChargement()
      }
      this.is_loading_categorie_faqs = false;
    }

    this.is_loading_categorie_faqs = false;
  },
  data() {
    return {
      constants,

      is_loading_categorie_faqs: false,
      is_doing_action: false,
      totalChargement: 0,
      actCharge: 0,
      categorie_faq: {
        id_categorie_faq: -1,
        libelle : ""
      },
      default_categorie_faq: {
        id_categorie_faq: -1,
        libelle : ""
      },
      list_categories: [],

      loading_message: "Chargement de données en cours",
    };
  },
  components: {},
};
</script>

<template>
  <section class="news-grid mt-5">
    <div class="page-header">
      <h3 class="page-title">
        {{ add_mode ? "Nouvelle Catégorie" : "Editer  une Catégorie" }}
      </h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <router-link :to="{ name: 'dashboard' }"
              >Administration</router-link
            >
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            {{ add_mode ? "Nouvelle Catégorie " : "Editer  une Catégorie" }}
          </li>
        </ol>
      </nav>
    </div>

    <div class="mt-5 pt-4" v-if="is_loading_categorie_faqs">
      <div class="card">
        <div class="card-body">
          <div class="circle-loader"></div>
          <p class="text-center mt-4">Chargement de données en cours</p>
        </div>
      </div>
    </div>
    <div class="row mt-5" v-else-if="!is_loading_categorie_faqs">
      <div class="col-md-12">
        <div class="card mb-3" v-if="is_doing_action">
          <div class="card-body">
            <div class="circle-loader"></div>
            <p class="text-center mt-4">{{ loading_message }}</p>
          </div>
        </div>

        <div class="card mb-4">
          <div class="card-body">
            <div>
              <div class="row">
                <div class="col-md-7">
                  <fieldset class="mt-4" :disabled="is_doing_action">

                    <b-form-group
                      label="Libellé :"
                      label-for="libelle"
                      class="mt-3"
                    >
                      <b-form-textarea
                        id="libelle"
                        placeholder="Libellé"
                        :rows="5"
                        :max-rows="5"
                        v-model="categorie_faq.libelle"
                      ></b-form-textarea>
                    </b-form-group>
 
                    <b-button
                      type="submit"
                      :disabled="is_doing_action"
                      variant="success"
                      class="mr-2 mt-4"
                      @click="add_mode ? doAddCategorieFaq() : doEditQuestion()"
                      >{{
                        add_mode
                          ? "Enregistrer"
                          : "Enregistrer les modifications apportées"
                      }}</b-button
                    >
                  </fieldset>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>
<style scoped>
.detail-categorie_faq-item {
  font-size: 1.08em;
}
.biographie-text {
  font-size: 1.1em;
}
</style>